/* General styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #ffffff; /* Match the background color */
}

.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.form-container {
  background: #ffffff; /* Match the form background color */
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 400px;
  text-align: center;
}

/* Header styles */
.page-header {
  margin-bottom: 20px;
}

.page-header__text-wrapper {
  text-align: center;
}

.heading {
  font-size: 36px;
  margin-bottom: 10px;
  color: #000000;
}

.page-header p {
  font-size: 16px;
  color: #666666;
}

/* Form styles */
.form {
  display: flex;
  flex-direction: column;
}

.input {
  position: relative;
  margin-bottom: 20px;
}

.input__field {
  width: 100%;
  padding: 12px 10px;
  border: 1px solid #cccccc;
  border-radius: 4px;
  font-size: 16px;
  outline: none;
  box-sizing: border-box;
}

.input__field:focus {
  border-color: #000000;
}

.input__label {
  position: absolute;
  top: -10px;
  left: 10px;
  background: #ffffff;
  padding: 0 5px;
  font-size: 14px;
  color: #666666;
}

.input__field-link {
  margin-top: 5px;
  font-size: 12px;
  color: #666666;
  background: none;
  border: none;
  cursor: pointer;
  text-decoration: underline;
}

.input__field-link.toggle-password {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
}

.input__field-link.forgot-password {
  display: block;
  margin-top: -10px;
  margin-bottom: 20px;
  text-align: right;
}

.button--primary {
  background-color: #000000;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 10px;
}

.button--primary:hover {
  background-color: #333333;
}

.button--full {
  width: 100%;
}

.form__secondary-action {
  display: block;
  margin-top: 15px;
  font-size: 14px;
  color: #666666;
}

.text--subdued {
  color: #666666;
}

.link {
  color: #000000;
  text-decoration: underline;
  cursor: pointer;
}

.link:hover {
  color: #333333;
}
.form__submit {
  padding: 10px;
  font-size: 16px;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  text-align: center;
  transition: background-color 0.3s;
}
