/* General styles */
body {
    font-family: Arial, sans-serif;
    background-color: #f9f9f9;
    margin: 0;
    padding: 0;
}

.reg-container {
    max-width: 500px;
    margin: 50px auto;
    padding: 20px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.page-header {
    text-align: center;
    margin-bottom: 20px;
}

.page-header__text-wrapper {
    margin-bottom: 20px;
}

.heading.h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.text-container p {
    font-size: 14px;
    color: #666;
}

.page-content {
    padding: 20px;
}

.page-content--small {
    padding: 20px;
}

.account__block-list {
    list-style: none;
    padding: 0;
    margin: 0;
}

.account__block-item {
    margin-bottom: 20px;
}

.form {
    display: flex;
    flex-direction: column;
}

.input {
    margin-bottom: 15px;
    position: relative;
}

.input__field {
    width: 100%;
    padding: 10px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

.input__label {
    position: absolute;
    top: 50%;
    left: 10px;
    transform: translateY(-50%);
    background-color: #fff;
    padding: 0 5px;
    font-size: 12px;
    color: #aaa;
    transition: 0.2s;
    pointer-events: none;
}

.input__field:focus + .input__label,
.input__field:not(:placeholder-shown) + .input__label {
    top: -10px;
    left: 10px;
    font-size: 10px;
    color: #333;
}

.form__submit {
    padding: 10px;
    font-size: 16px;
    color: #fff;
    background-color: #000000;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    text-align: center;
    transition: background-color 0.3s;
}

.form__submit:hover {
    background-color: #060606;
}

.form__secondary-action {
    display: block;
    text-align: center;
    margin-top: 20px;
    font-size: 14px;
    color: #666;
}

.form__secondary-action .link {
    color: #000000;
    text-decoration: none;
    margin-left: 5px;
}

.form__secondary-action .link:hover {
    text-decoration: underline;
}

.input__field-link.toggle-password {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    font-size: 12px;
    color: #666;
    background: none;
    border: none;
    cursor: pointer;
    text-decoration: underline;
}

/* Additional styles if needed */
.text--xsmall {
    font-size: 12px;
}
